.App {
  text-align: center;
  font-family: -apple-system,'Inter UI', sans-serif;
    background-color: rgb(28, 29, 31);
  /* background-color: #282c34; */
  color: white;
  height: 100vh;

}

.App p {
  font-size: 14px;
}

.card {
      width: calc(33.333% - 28px);
      margin: 24px;
      min-width: 200px;
      min-height: 300px;
      max-height: 500px;
      padding: 52px;
      background: linear-gradient(rgb(40, 41, 44) 0%, rgba(39, 40, 43, 0.82) 100%);
      box-shadow: rgb(0 0 0 / 30%) 0px 13px 40px,
      rgb(0 0 0 / 20%) 0px 4px 4px;
      border-radius: 48px;
      overflow: scroll;
      overflow-x: hidden;
     -webkit-appearance: none;
}



/* scrollbar */

.card::-webkit-scrollbar {
  width: 20px;
  height: 10px;
  top: 10px;
  color:gray; 
  scroll-padding: 10px;       
}

.card::-webkit-scrollbar-track {
  background: transparent;
  scrollbar-shadow-color: azure; 
  margin: 10px;
   width: 10px;
   height: 100px;
}

.card::-webkit-scrollbar-track-piece {
  background: transparent center 0 repeat-y;
  margin-top: 100px;
  margin-bottom: 100px; 
    /* background: url('scroll-bg.gif') center 0 repeat-y; */
}


.card::-webkit-scrollbar-thumb {
  -webkit-appearance: none;
  margin-top: 100px;
  height: 100px;
  width: 100px;
  max-height: 10px;
  background-color: rgb(155, 155, 155);
  padding-top: 5px;
  top:15px;
  border-radius: 50px;       
  border: 2px solid rgb(56, 56, 56); 
}

.card::-webkit-scrollbar-thumb:hover {
  background-color: #f8f8f8;
}


.user {
  display: flex;
}
.user p{ 
  color: gray;
  font-weight: 500;
  
}

.user h{
  color: rgb(69, 81, 181);
  font-weight: 500;
  padding-left: 2px;
  
}

.titles {
  margin: 2px;
  color: rgb(252, 252, 252);
  padding-left: 0px;
  margin-left:0px;
  text-align: start;
}

.taskNumber{
  color: gray;
   text-align: start;
}

