} */

.btn { 
    width: 50%;
    max-width: 200px;
    background: linear-gradient(101.06deg, rgb(69, 81, 181) 9.16%, rgb(86, 67, 204) 32.02%, rgb(84, 63, 215) 64.72%);
    border:none;
    border-radius:4px;
    color: white;
    height: 36px;
    line-height: 36px;
    text-align: center;
    font-size: 13px;
    font-weight: 500;
  
}


.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    /* animation: App-logo-spin infinite 20s linear; */
    animation: gradient 8s infinite ease-in-out;
  }
}

@keyframes gradient {
  from {
    /* background-color: red; */
     filter: saturate(1);
     filter: hue-rotate(360deg);
  }

  to {
    /* background-color: blue; */
     filter: saturate(0);
     filter: hue-rotate(0deg);
  }
}

.container {
  position: relative;
  text-align: center;
  color: rgb(231, 229, 229);
}

.container img{
  border: 5px;
}

.centered {
  position: absolute;
  top: 50%;
  left: 50%;
  font-weight: 500;
  font-size:17px;
  transform: translate(-50%, -50%);
}

.centered p {
font-size:14px;
}

.App-header {
    background-color: rgb(28, 29, 31);
  /* background-color: #282c34; */
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.Logo {
  width: 26px;
  padding-left: 5px;
}
.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
